import { BottomScrollListener } from 'react-bottom-scroll-listener';
import { useState,useEffect } from 'react';
import Loading from '../utils/Loading';
import Footer from './Footer';
import { useHistory } from 'react-router-dom'
import { onSnapshot,collection,query,where } from 'firebase/firestore';
import { db } from './Firebase';



function Blog() {

    const router = useHistory();
    const [counter, setCounter] = useState(3);
    const [blogAxios, setBlogAxios] = useState([])
    const [blogsFinished, setblogsFinished] = useState(false)
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        onSnapshot(query(collection(db, 'blogs')), where('status', '==', 'Publish'), snapshot => setBlogAxios(snapshot.docs), setLoading(false))
    }, [])

    const handleOnDocumentBottom = () => {
        const newCounter = counter + 3
        if (newCounter > blogAxios.length) {
            setCounter(counter+3)
        }
        else {
            setCounter(blogAxios.length)
            setblogsFinished(true)
        }
    }

    const readBlog = (id) => {
        router.push(`/read/${id}`)
    }

    if (loading) {
        return <Loading/>
    }
    else {
        return (
            <main id='blog' className='main'>
                <div className='main-div'>
                    <div className='main-div_blog'>
                        <h1 className='main-div_blog-header'>
                            Blogs
                        </h1>
                    </div>
                        <div className='main-div_blogs'>
                            {blogAxios.map((blog) => (
                                <div key={blog.id} className='main-div_blogs-container' onClick={() => { readBlog(blog.id) }}>
                                    <img className='main-div_blogs-image' alt={blog.data().imageDescription} src={blog.data().imageSource || 'https://www.goodcore.co.uk/blog/wp-content/uploads/2019/08/coding-vs-programming-2.jpg'} />
                                    <div style={{ display: 'flex', flexDirection: 'row',justifyContent: 'space-between' }}>
                                        <div style={{height: 'fit-content'}} className='main-div_blogs-container-tags-container'>
                                            {blog.data().tags.map(tag => { return <div style={{ color: 'black' }} key={tag} className='main-div_blogs-container-tags'>#{tag}</div> })
                                            }
                                        </div>
                                        <div style={{width: 'fit-content',display: 'flex',flexDirection: 'column', justifyContent: 'space-between', textAlign: 'right',padding:'0',margin:'0' }}>
                                            <div>{blog.data().time}</div>
                                            <div className='readTime'>
                                                {blog.data().readTime}
                                            </div>
                                        </div>
                                    </div>
                                        <h3 className='main-div_blogs-container-title'>
                                            {blog.data().title}
                                        </h3>
                                </div>
                            ))}
                        </div>
                </div>
                {blogsFinished ? (
                    <Footer />
                ) : (
                    <BottomScrollListener onBottom={handleOnDocumentBottom} />
                )}
                
            </main>
        )
    }
}

export default Blog
