import DOMPurify from 'dompurify';
import { useParams } from 'react-router-dom';
import { useEffect,useState } from 'react';
import { Helmet } from 'react-helmet';
import { onSnapshot,collection,query,where } from 'firebase/firestore';
import { db } from '../components/Firebase';


function BlogView() {
  const [blogData, setBlogData] = useState([]);
  const { id } = useParams();
  useEffect(() => {
    onSnapshot(query(collection(db, 'blogs')), where('id', '==', id), snapshot => setBlogData(snapshot.docs))
  }, [id])

  return (
    <div className='blogView'>
      {blogData.slice(0,1).map((blog) => (
        <>
            <Helmet>
                <meta charSet='utf-8'></meta>
                <title>{blogData[0].data().title || 'Coding Blog to land a job in FAANG.'}</title>
                <link rel='stylesheet' type='text/css' href={blogData[0].data().cssRef || null}></link>
                <meta name='description' content={blogData[0].data().pageDescription || "Blog that helps students to land a job in FAANG. Read more find out how to land a job in FAANG"}></meta>
              </Helmet>
              <div
                style={{ width: '100vw', height: '100vh', overflow: 'scroll' }} dangerouslySetInnerHTML={{ __html: DOMPurify.sanitize(blogData[0].data().message) }}
              >
              </div> 
          </>
      ))}
    </div>
  )
}

export default BlogView
